import React from "react";
import config from "../../api/config";
import Section from "../../components/Section";
import Icon from "../../components/Icon";
import './Front.scss';

function Address(props) {
  const {address} = props;
  if (Array.isArray(address)) {
    return (
      <div className="address">
        {address.map((addr, i) => (
          <React.Fragment key={i}>
            {addr}<br/>
          </React.Fragment>
        ))}
      </div>
    );
  }
  return (<div className="address">{address}</div>);

}

class _Front extends React.Component {
  render() {
    const { name, contact } = this.props;
    const { address, mobile, email, website } = contact || {};
    const mobiles = mobile && (Array.isArray(mobile) ? mobile : [mobile]);
    return (
      <Section id="info">
        <div className="d-flex">
          <h1 className="mr-auto name">{name}</h1>
          <div className="contact">
            {address && <Address address={address} />}
            {mobiles && 
              mobiles.map((mobile, i) => (
                <div className="mobile" key={i}>
                  <Icon icon="phone"/>{mobile}
                </div>
              ))
            }
                {/* <span dangerouslySetInnerHTML={{__html: markedInline(mobile)}}/> */}
            {email && 
              <div className="email">
                <Icon icon="envelope"/>{email}
                {/* <span dangerouslySetInnerHTML={{__html: markedInline(mobile)}}/> */}
              </div>}
            {website && 
              <div className="website">
                <Icon icon="home"/>{website}
                {/* <span dangerouslySetInnerHTML={{__html: markedInline(mobile)}}/> */}
              </div>}
          </div>
        </div>
      </Section>
    );
  }
}

export default function Front(props) {
  const info = config.info;
  const name = info.name;
  const contact = info.contact;
  return <_Front name={name} contact={contact} {...props} />;
}
